<template>
  <div id="app">
    <transition name="modal-transition">
      <div class="modal-mask" v-if="currentElement">
        <div class="modal-close" @click="currentElement = undefined" />
        <div class="modal">
          <div class="element">
            <div class="symbol">{{ currentElement.symbol }}</div>
            <div class="name">{{ currentElement.name }}</div>
            <div class="shells">
              <div
                class="orbit"
                v-for="(shell, shellindex) in reverseShell"
                :key="shellindex"
              >
                <div
                  class="electron"
                  v-for="(_, index) in shell"
                  :key="index"
                ></div>
              </div>
            </div>
          </div>
          <div class="details">
            <div class="summary">{{ currentElement.summary }}</div>
            <table>
              <tr><th>atomic mass</th><td><div>{{ currentElement.atomic_mass ? currentElement.atomic_mass : "N/A" }}</div></td></tr>
              <tr><th>category</th><td><div>{{ currentElement.category ? currentElement.category : "N/A" }}</div></td></tr>
              <tr><th>phase</th><td><div>{{ currentElement.phase ? currentElement.phase : "N/A"}}</div></td></tr>
              <tr><th>density</th><td><div>{{ currentElement.density ? currentElement.density : "N/A"}} <span v-if="currentElement.density">g/cm<sup>3</sup></span> </div></td></tr>
              <tr><th>appearance</th><td><div>{{ currentElement.appearance ? currentElement.appearance : "N/A" }}</div></td></tr>
              <tr><th>shells</th><td><div>[ {{ currentElement.shells ? currentElement.shells.join(" | ") : "N/A" }} ]</div></td></tr>
              <tr><th>discovered by</th><td><div>{{ currentElement.discovered_by ? currentElement.discovered_by : "N/A"}}</div></td></tr>
              <tr v-if="currentElement.named_by"><th>named by</th><td><div>{{ currentElement.named_by }}</div></td></tr>
            </table>
          </div>
        </div>
      </div>
    </transition>
    <div class="table">
      <div
        v-for="(element, index) in table.elements"
        :key="index"
        :style="{
          'grid-row': element.ypos,
          'grid-column':
            element.category === 'lanthanide' || element.category === 'actinide'
              ? element.xpos + 1
              : element.xpos,
          'padding-bottom': '100%',
          position: 'relative',
        }"
      >
        <table-element
          :click="(el) => (currentElement = el)"
          :dir="
            dir(
              element.category === 'lanthanide' ||
                element.category === 'actinide'
                ? element.xpos + 1
                : element.xpos,
              element.ypos
            )
          "
          :element="element"
        />
      </div>
      <div
        :style="{
          'grid-row': 6,
          'grid-column': 3,
          'padding-bottom': '100%',
          position: 'relative',
        }"
      >
        <div class="table-element dark-lanthanide">
          <div class="border nohover">57-71</div>
        </div>
      </div>
      <div
        :style="{
          'grid-row': 7,
          'grid-column': 3,
          'padding-bottom': '100%',
          position: 'relative',
        }"
      >
        <div class="table-element dark-actinide">
          <div class="border nohover">89-103</div>
        </div>
      </div>
      <div style="grid-area: 8 / 3 / 10 / 3">
        <div class="arrow" />
      </div>
    </div>
  </div>
</template>

<script>
import TableElement from "./components/TableElement";

export default {
  name: "App",
  components: {
    TableElement,
  },
  data: () => ({
    table: undefined,
    currentElement: undefined,
  }),
  methods: {
    dir(x, y) {
      if (x === 1 && y === 1) return "top-left";
      else if (x === 18 && y === 1) return "top-right";
      else if (x === 18 && y === 10) return "bottom-right";
      else if (x === 1 && y === 10) return "bottom-left";
      else if (x === 1) return "left";
      else if (x === 18) return "right";
      else if (y === 1) return "top";
      else if (y === 10) return "bottom";
      else return "";
    },
  },
  computed: {
    reverseShell() {
      var arr = [];
      this.currentElement.shells.forEach((x) => {
        arr.push(x);
      });
      return arr.reverse();
    },
  },
  mounted() {
    fetch(
      "https://raw.githubusercontent.com/Bowserinator/Periodic-Table-JSON/master/PeriodicTableJSON.json"
    )
      .then((res) => res.json())
      .then((data) => {
        this.table = data;
      });
  },
};
</script>

<style lang="scss">
