      }

      .shells {
        width: 25vw;
        height: 25vw;
        margin: auto;
        position: relative;

        $orbit-spacing: 12;

        @function orbit($n) {
          @return percentage(1 - ($n / $orbit-spacing));
        }
        @function orbitm($n) {
