<template>
  <div :class="['table-element', category]">
    <div :class="['border', dir]" @click="click(element)">
      <div class="order">{{ element.number }}</div>
      <div class="symbol">{{ element.symbol }}</div>
      <div class="name">
        <span>{{ element.name }}</span>
      </div>
      <div class="mass">
        {{ Math.round((element.atomic_mass + Number.EPSILON) * 100) / 100 }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TableElement",
  props: {
    element: Object,
    dir: String,
    click: Function,
  },
  computed: {
    category() {
      return this.element.category.replaceAll(" ", "-").split(",")[0];
    },
  },
};
</script>

